/* eslint-disable react/prop-types, react/jsx-handler-names */

import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import {withStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import MenuItem from '@material-ui/core/MenuItem';
import CancelIcon from '@material-ui/icons/Cancel';
import {emphasize} from '@material-ui/core/styles/colorManipulator';
import AsyncSelect from 'react-select/lib/Async';
import {debounce} from 'lodash';


const styles = theme => ({
    root: {
        flexGrow: 1,
        height: 250,
    },
    input: {
        display: 'flex',
        padding: 0,
        color: 'white'
    },
    valueContainer: {
        display: 'flex',
        flexWrap: 'wrap',
        flex: 1,
        alignItems: 'center',
    },
    chip: {
        margin: `${theme.spacing.unit / 2}px ${theme.spacing.unit / 4}px`,
    },
    chipFocused: {
        backgroundColor: emphasize(
            theme.palette.type === 'light' ? theme.palette.grey[300] : theme.palette.grey[700],
            0.08,
        ),
    },
    noOptionsMessage: {
        padding: `${theme.spacing.unit}px ${theme.spacing.unit * 2}px`,
    },
    singleValue: {
        fontSize: 16,
    },
    placeholder: {
        position: 'absolute',
        left: 2,
        fontSize: 16,
    },
    paper: {
        position: 'absolute',
        zIndex: 1,
        marginTop: theme.spacing.unit,
        left: 0,
        right: 0,
    },
    divider: {
        height: theme.spacing.unit * 2,
    },
});

function NoOptionsMessage(props) {
    return (
        <Typography
            color="textSecondary"
            className={props.selectProps.classes.noOptionsMessage}
            {...props.innerProps}
        >
            {props.children}
        </Typography>
    );
}

function inputComponent({inputRef, ...props}) {
    return <div ref={inputRef} {...props} />;
}

function Control(props) {
    return (
        <TextField
            fullWidth
            InputProps={{
                inputComponent,
                inputProps: {
                    className: props.selectProps.classes.input,
                    inputRef: props.innerRef,
                    children: props.children,
                    ...props.innerProps,
                },
            }}
            {...props.selectProps.textFieldProps}
        />
    );
}

function Option(props) {
    return (
        <MenuItem
            buttonRef={props.innerRef}
            selected={props.isFocused}
            component="div"
            style={{
                fontWeight: props.isSelected ? 500 : 400,
            }}
            {...props.innerProps}
        >
            {props.children}
        </MenuItem>
    );
}

function Placeholder(props) {
    return (
        <Typography
            color="textSecondary"
            className={props.selectProps.classes.placeholder}
            {...props.innerProps}
        >
            {props.children}
        </Typography>
    );
}

function SingleValue(props) {
    return (
        <Typography className={props.selectProps.classes.singleValue} {...props.innerProps}>
            {props.children}
        </Typography>
    );
}

function ValueContainer(props) {
    return <div className={props.selectProps.classes.valueContainer}>{props.children}</div>;
}

function MultiValue(props) {
    return (
        <Chip
            tabIndex={-1}
            label={props.children}
            className={classNames(props.selectProps.classes.chip, {
                [props.selectProps.classes.chipFocused]: props.isFocused,
            })}
            onDelete={props.removeProps.onClick}
            deleteIcon={<CancelIcon {...props.removeProps} />}
        />
    );
}

function Menu(props) {
    return (
        <Paper square className={props.selectProps.classes.paper} {...props.innerProps}>
            {props.children}
        </Paper>
    );
}

const components = {
    Control,
    Menu,
    MultiValue,
    NoOptionsMessage,
    Option,
    Placeholder,
    SingleValue,
    ValueContainer,
};

const promiseOptions = (latitude, longitude, inputValue) => {
    if (!inputValue) {
        return;
    }
    return fetch(`/api/typeahead/products?query=${inputValue}`, {
        headers: {
            "Authorization": "c9764fd946b54b9195adc4b7a2ca58cd",
            latitude,
            longitude
        },

    })
        .then(response => response.json())
        .then(response => {
            return response && response.map(i => {
                return {
                    ...i,
                    label: i.name,
                    value: i.id
                }
            })
        })
};

class IntegrationReactSelect extends React.Component {
    constructor(props) {
        super(props);
        this.props = props;
    }

    state = {
        currentValue: "",
        single: null,
        multi: null,
    };

    handleChange = (selectedOption) => {
        this.setState({selectedOption});
        this.props.onSelected(selectedOption);
    }

    handleFreeformEvent() {
        this.props.onSelected({name: this.state.currentValue})
    }

    render() {
        const {classes, theme} = this.props;
        const opt = promiseOptions.bind(null, this.props.latitude, this.props.longitude);
        const selectStyles = {
            input: base => ({
                ...base,
                color: theme.palette.text.primary,
            }),
        };

        return (
            <AsyncSelect classes={classes}
                         styles={{control: {width: 700, color: 'white'}}}
                         noOptionsMessage={() => null}
                         loadingMessage={() => null}
                         components={components}
                         cacheOptions
                         defaultOptions
                         autoLoad={false}
                         onInputChange={(currentValue) => this.setState({currentValue})}
                         noOptionsMessageCSS={() => {
                             display: 'none'
                         }}
                         onKeyDown={(event) => {
                             if (event.keyCode === 13) {
                                 this.handleFreeformEvent();
                             }
                         }}
                         ref={a => {
                             const reffee = a
                         }}
                         onChange={this.handleChange}
                         loadOptions={debounce(opt, 250, {leading: true, trailing: true})}/>


        );
    }
}

IntegrationReactSelect.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
};

export default withStyles(styles, {withTheme: true})(IntegrationReactSelect);